.question-page {
  height: auto;
  width: 100%;
  margin-top: -2%;
  background: url('../assets/background.png');
  background-repeat: no-repeat;
  background-size: cover;
}

.question-header {
  width: 80%;
  padding-left: 3%;
  padding-top: 2%;
  margin: 2% auto;
  font-size: 2rem;
  font-weight: 600;
  font-family: Comic Sans MS;
  border-bottom: 2px solid #00a2ff;
}

.question-container {
  width: 80%;
  margin: 1% auto;
}

.question-description,
.format,
.sample-input {
  width: 100%;
  padding: 1%;
  margin: 1% auto;
  text-align: justify;
  border-radius: 1rem;
  line-height: 1.5rem;
  font-size: 1rem;
}

.question-container p,
.question-container h3 {
  font-family: Comic Sans MS;
}

.stage-form-container {
  width: 80%;
  margin: 3% auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.stage-form {
  width: 35%;
}

.input-field-tags label {
  display: flex;
  align-items: center;
}

.stage-input-fields {
  outline: 1px solid #000000;
  color: #000000;
}

.coding-image-container {
  width: 45%;
  height: 40vh;
}

.coding-image-container > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.pagination-button-container {
  width: 80%;
  margin: 2% auto;
  padding: 5% 0%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}

.pagination-button-container a {
  text-decoration: none;
}

.previous-button,
.next-button,
.continue-button {
  padding: 1% 3%;
  font-size: 1.2rem;
  border: none;
  color: #ffffff;
  background-color: #00a2ff;
  border-radius: 0.5rem;
  cursor: pointer;
}

.continue-button {
  padding: 2% 3%;
}

.left-arrow {
  animation: leftarrow 2s infinite;
}

@keyframes leftarrow {
  0%,
  40%,
  100% {
    transform: translateX(0);
  }
  20% {
    transform: translateX(-10px);
  }
}

.right-arrow {
  animation: rightarrow 2s infinite;
}

@keyframes rightarrow {
  0%,
  40%,
  100% {
    transform: translateX(0);
  }
  20% {
    transform: translateX(10px);
  }
}

@media screen and (max-width: 450px) {
  .stage-form-container {
    flex-direction: column-reverse;
  }
  .coding-image-container {
    height: 20vh;
    width: 100%;
    margin-top: 5%;
  }
  .stage-form {
    margin-top: 5%;
    width: 100%;
  }
  .input-fields {
    width: 19rem;
  }
  .pagination-button-container {
    margin: 20% auto;
  }
  .previous-button,
  .next-button,
  .continue-button {
    padding: 3% 5%;
  }
}
